<template>
	<view class="content">
		<view class="circle">
			<view class="circle_left ab" :style="leftRate"></view>
			<view class="circle_right ab" :style="rightRate"></view>
			<view class="circle_text">
				<span class="value">{{progress}}</span>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "CircleProgress",
		data() {
			return {
				progress:0, //进度百分比
				leftRate:'',
				rightRate:'',
			}
		},
		props:{
			percentage:{
				type: Number
			}
		},
		mounted() {
			this.progress = this.percentage
			if (this.progress <= 50) {
				this.leftRate = 'transform: rotate(0);';
				this.rightRate = 'transform: rotate('+3.6 * this.progress + 'deg);'
			} else {
				this.leftRate = 'transform: rotate('+3.6 * (this.progress-50) + 'deg);';
				this.rightRate = 'transform: rotate(0);border-color: #ffd656;'
			}
		}
	}
</script>

<style lang="scss">
	.circle {
	    width: 160rpx;
	    height: 160rpx;
	    position: relative;
	    border-radius: 50%;
	    box-shadow: inset 0 0 0 20rpx #ffd656;
		margin: 20rpx auto;
		color: #ffd656;
		
	    .ab {
	        position: absolute;
	        left: 0;
	        right: 0;
	        top: 0;
	        bottom: 0;
	        margin: auto;
	    }
	
	    &_left {
	        border: 20rpx solid #ececec;
	        border-radius: 50%;
	        clip: rect(0, 80rpx, 160rpx, 0);
	    }
	
	    &_right {
	        border: 20rpx solid #ececec;
	        border-radius: 50%;
	        clip: rect(0, 160rpx, 160rpx, 80rpx);
	    }
	
	    &_text {
	        height: 100%;
	        display: flex;
	        flex-direction: column;
	        justify-content: center;
	        align-items: center;
			.value{
				font-weight: 600;
				font-size: 38rpx;
			}
			.value::after{
				content: '%';
				display: inline-block;
				font-weight: 400;
				font-size: 24rpx;
			}
	    }
	}
</style>